<template>
  <div class="modal-document">
    <div v-if="lang===`pl`">
      <h4 class="fw-bold">Ewakuacja oznaczała życie</h4>
      <p class="fw-bold">
        Sytuacja Polskich Sił Zbrojnych w ZSRS – jak oficjalnie nazwano Armię generała Władysława Andersa – od początku
        była
        trudna. Poprawiała się nieco, kiedy Niemcy odnosili kolejne sukcesy na froncie. Gdy zostali odparci od Moskwy,
        Stalin zdecydował się definitywnie pozbyć niewygodnego dlań wojska.
      </p>
      <p>
        Osiemnastoletni Artur Gruber, jeden z tysięcy Polaków uwięzionych po 17 września 1939 roku w sowieckim gułagu,
        wspominał, że kiedy stanął przed wojskową komisją lekarską, wyglądał jak szkielet odziany w łachmany.
        „Niemiłosiernie brudny szkielet” – dodał. Badający go młody lekarz, w pewnym momencie zapytał swego starszego
        kolegę: „Dlaczego oni wszyscy mają takie czerwone kropki jedną przy drugiej?”.
      </p>
      <p>
        Ten lekarz musiał przybyć z Wielkiej Brytanii, bo gdyby był jednym z zesłańców czy łagierników w Rosji,
        wiedziałby,
        że to ślady po ukąszeniach wszy… Tak wyglądała większość rekrutów zgłaszających się do punktów rekrutacyjnych
        Armii
        generała Andersa. Ich sytuacja w obozie wojskowym polepszyła się tylko nieznacznie. Nadal cierpieli głód i zimno,
        gdyż sowieccy sojusznicy nie przysyłali ani wystarczających racji żywnościowych, ani zaopatrzenia.
        Sytuacji nie poprawiła dyslokacja Armii do Uzbekistanu, Kirgizji i Kazachstanu pod koniec grudnia 1941 roku.
        Wprawdzie żołnierze i ich rodziny nie wystawieni byli już na mrozy, ale szybko zaczęły dziesiątkować ich choroby
        zakaźne. Znacznie pogorszyły się też warunki zakwaterowania, a przede wszystkim wyżywienie. A to ostatnie, jak się
        okazało, miało zostać jeszcze bardziej ograniczone.
      </p>

      <h4 class="fw-bold">W matni</h4>
      <p>
        W czasie spotkania Naczelnego Wodza, generała Władysława Sikorskiego z Józefem Stalinem na początku grudnia 1941
        roku ustalono, że ze Związku Sowieckiego ewakuowanych zostanie 25 tysięcy polskich żołnierzy – lotników i
        specjalistów różnych broni, których brakowało Polskim Siłom Zbrojnym na Zachodzie. Do tego jak ewakuować tych
        ludzi
        do Wielkiej Brytanii i na Bliski Wschód doszedł generałowi Andersowi kolejny, jeszcze poważniejszy problem.
        Mianowicie Sowieci zaczęli naciskać, by wysłał na front 5 Dywizję Piechoty. Była to jedyna w Armii Andersa w miarę
        uzbrojona dywizja. Reszty jednostek ani Rosjanie, ani Brytyjczycy nie chcieli wyposażać, a wszelkie rozmowy na ten
        temat nie dawały żadnego efektu. Polacy po przykrych doświadczeniach we Francji w 1940 roku, bronili się przed
        podziałem Armii i wysyłaniem na front jej pojedynczych dywizji. Generał Anders więc, za zgodą generała
        Sikorskiego,
        zdecydowanie odmówił.
      </p>
      <p>
        Otrzymał na to odpowiedź 6 marca 1942 roku. Szef zaopatrzenia Armii Czerwonej, generał Andriej Chrulew zawiadomił
        o
        drastycznym zmniejszeniu racji żywnościowych do stanu 26 tysięcy. To oznaczało głód dla wielu żołnierzy i prawie
        wszystkich cywilów, zgromadzonych przy Armii. Generał Anders natychmiast postanowił interweniować u Stalina,
        argumentując, że narusza to porozumienie wynegocjowane przez generała Sikorskiego. Stalin przyjął Andersa na
        audiencji 18 marca i zgodził się podwyższyć rację żywnościowe do 44 tysięcy. To miało wystarczyć dla trzech
        dywizji
        i pułku zapasowego. Reszta jednostek miała być ewakuowana na Bliski Wschód.
      </p>

      <h4 class="fw-bold">Nie patrzeć za siebie</h4>
      <p>
        Kilkadziesiąt minut po zakończeniu rozmowy ze Stalinem, generał Anders spotkał się z generałem NKWD Gieorgijem
        Sergiejowiczem Żukowem, pełnomocnikiem rządu sowieckiego do spraw formowania wojsk polskich w ZSRS. Ku zdziwieniu
        polskiego dowódcy, Żukow miał przy sobie komplet dokumentów do ewakuacji około 30 tysięcy żołnierzy i 10 tysięcy
        członków ich rodzin, czyli tej nadwyżki ludzi, którym Sowieci nie zapewniali wyżywienia. Jak podkreśla profesor
        Edward Kospath-Pawłowski, „Przemawia to za tezą, że Stalin dążył do ewakuacji polskich oddziałów z ZSRR, a sama
        ewakuacja miała rozpocząć się w kilkadziesiąt godzin później [od spotkania z Andersem – PK]”.
        Pierwsze rozkazy o ewakuacji generał Anders wydał jeszcze w Moskwie, 19 marca. Ewakuacja miała przebiegać przez
        bazę
        w Krasnowodsku, której dowódcą generał mianował podpułkownika Zygmunta Berlinga. Pierwszy transport odpłynął stąd
        do
        irańskiego Pehlewi 24 marca. Oddajmy w tym miejscu głos wspomnianemu na początku Arturowi Gruberowi: „Na pokładzie
        sowieckiego tankowca opuszczaliśmy bez żalu kraj niespełnionych nadziei i bardzo smutnych, szarych ludzi.
        Pozostawiliśmy po sobie cmentarz. Epidemie tyfusu plamistego i brzusznego oraz krwawej dyzenterii zbierały obfite
        żniwo wśród wycieńczonych Polaków. Niektórzy twierdzą, że spoczywa ich tam więcej niż pod górą Monte Cassino.
        Drugiego dnia wieczorem ujrzeliśmy brzegi Iranu. Port Pehlewi był skąpo oświetlony światłem elektrycznym. Po
        zejściu
        na ląd słysząc polską mowę uwijających się sanitariuszy przyjmujących chorych z naszego transportu, poczułem się
        jak
        w Polsce, wśród swoich, bezpiecznie”.
      </p>
      <p>
        Ewakuacja ta trwała do 5 kwietnia. Do Iranu (wtedy nazywanego Persją) dotarli wówczas żołnierze 8, 9 i 10 Dywizji
        Piechoty, brygady pancernej, pułków ułanów i saperów, kompani saperów kolejowych oraz większa część Ośrodka
        Organizacyjnego Armii, nadwyżki 6 i 7 Dywizji Piechoty. Ponadto do Pehlewi zaokrętowano wszystkich lotników i
        marynarzy polskich, jakich zdołano w Rosji odnaleźć wśród żywych.
      </p>

      <h4 class="fw-bold">Wychodzimy wszyscy</h4>
      <p>
        Szybko okazało się, że to nie koniec ewakuacji polskich wojsk ze Związku Sowieckiego. 14 maja Wiaczesław Mołotow w
        nocie do polskiego ambasadora w Moskwie, Stanisława Kota, podkreślił, że nie widzi możliwości prowadzenia dalszego
        zaciągu do polskiej Armii, skoro ma ona być wysłana poza granice ZSRS. To stwierdzenie było pokłosiem wizyty
        Mołotowa w Londynie, podczas której – bez wiedzy Polaków – Brytyjczycy zaproponowali, by przesunąć pozostałe w
        Rosji
        wojska polskie na Bliski Wschód. Zwolennikiem takiego rozwiązania był również generał Anders, który słał do sztabu
        Naczelnego Wodza raporty o coraz gorszej sytuacji aprowizacyjnej swoich oddziałów. Natomiast generał Sikorski
        nadal
        wiązał swe nadzieje z obecnością polskich żołnierzy na froncie wschodnim. Jak miało się okazać, decyzję o losie
        polskiej Armii w ZSRS, podjęto bez wiedzy obu generałów i niezależnie od ich opinii na ten temat.
        Pod koniec czerwca 1942 roku Mołotow zadepeszował do Londynu, że rząd sowiecki zgadza się na oddanie trzech
        polskich
        dywizji pozostających na terytorium sowieckim. Churchill podziękował Stalinowi za polskich żołnierzy, których „tak
        uprzejmie ofiarował”. Brytyjski premier miał się z czego cieszyć, gdyż Polacy mogli zapewnić mu osłonę irańskich i
        irackich rafinerii i rurociągów naftowych, które były strategiczne dla prowadzonych działań na froncie
        afrykańskim.
        O tych decyzjach generał Anders dowiedział się z prawie miesięcznym opóźnieniem, a 26 lipca otrzymał oficjalne
        pismo, w którym podkreślono, że „Rząd ZSRR zgadza się uczynić zadość staraniom Dowódcy Armii Polskiej w ZSRR […] o
        ewakuację oddziałów polskich z ZSRR na teren Bliskiego Wschodu i nie ma zamiaru stawiania jakichkolwiek przeszkód
        w
        natychmiastowym urzeczywistnieniu ewakuacji”. 31 lipca polsko-sowiecka komisja wojskowa ustaliła, że w ciągu
        sierpnia z Krasnowodzka do Pehlewi wyjedzie około 70 tysięcy wojskowych i cywilów. W protokole ewakuacyjnym
        podpisanym przez generała Andersa znalazło się między innymi zdanie, że „Rząd Polski nie uważa za możliwe użycie
        na
        froncie radziecko-niemieckim oddziałów formowanych w ZSRR”. To sformułowanie posłużyło Sowietom do faktycznego
        wstrzymania dalszego naboru do polskiej Armii. W obu akcjach ewakuacyjnych na Bliski Wschód ewakuowano 116543
        ludzi, w tym 78631 wojskowych. Dla ludzi, którzy w ten sposób mogli opuścić „nieludzką ziemię” generał Anders stał
        się mężem opatrznościowym i jak celnie podkreślił profesor Czesław Brzoza: „… swego dowódcę żołnierze traktowali
        jak
        Mojżesza, który wyprowadził ich z «domu niewoli»”.
      </p>

      <h4 class="fw-bold">Bibliografia</h4>
      <p>
        Czesław Brzoza, „Polska w czasach niepodległości i drugiej wojny światowej (1918–1945)”, Kraków 2001<br>
        Zbigniew Dunin-Wilczyński, „Wojsko Polskie w Iraku 1942–1943”, Warszawa 1993<br>
        Artur Gruber, „W imię Boże”, maszynopis w zbiorach autora<br>
        Edward Kospath-Pawłowski, „Wojsko Polskie na Wschodzie 1943–1945”, Pruszków 1997<br>
      </p>
    </div>
    <div v-else-if="lang===`en`">
      <h4 class="fw-bold">Evacuation meant life</h4>
      <p class="fw-bold">
        The situation of the Polish Armed Forces in the USSR - as General Władysław Anders' Army was officially called - was difficult from the beginning. It improved somewhat as the Germans achieved further success at the front. After they were repelled from Moscow, Stalin decided to get rid of the army that was inconvenient for him.
      </p>
      <p>
        Eighteen-year-old Artur Gruber, one of the thousands of Poles imprisoned in the Soviet gulag after September 17, 1939, recalled that when he stood before a military medical board, he looked like a skeleton clad in rags. "An mercilessly dirty skeleton," he added. A young doctor examining him, at one point asked his older colleague: "Why do they all have such red dots one next to the other?"
      </p>
      <p>
        This doctor must have come from Great Britain, because if he had been one of the exiles or labor camps in Russia, he would have known that these were the traces of lice bites ... This is how most of the recruits reporting to General Anders' Army recruiting points looked like. Their situation in the military camp improved only slightly. They still suffered from hunger and cold, as the Soviet allies were sending neither sufficient rations nor supplies. The situation was not improved by the dislocation of the Army to Uzbekistan, Kyrgyzstan and Kazakhstan at the end of December 1941. Although the soldiers and their families were no longer exposed to frost, infectious diseases quickly began to decimate them. The conditions of accommodation, especially food, also deteriorated significantly. And the latter, as it turned out, was to be even more limited.
      </p>

      <h4 class="fw-bold">

In the snare
</h4>
      <p>
        During the meeting of the Commander-in-Chief, General Władysław Sikorski, with Józef Stalin at the beginning of December 1941, it was established that 25,000 Polish soldiers - airmen and specialists of various weapons, who were missing from the Polish Armed Forces in the West, would be evacuated from the Soviet Union. In addition to how to evacuate these people to Great Britain and the Middle East, General Anders faced another, even more serious problem. Namely, the Soviets began to pressure him to send the 5th Infantry Division to the front. It was the only reasonably armed division in Anders' Army. The rest of the units, neither the Russians nor the British, wanted to equip, and any talks on this subject had no effect. After unpleasant experiences in France in 1940, Poles defended themselves against the division of the Army and sending its individual divisions to the front. So General Anders, with the consent of General Sikorski, decisively refused.
      </p>
      <p>
       During the meeting of the Commander-in-Chief, General Władysław Sikorski, with Józef Stalin at the beginning of December 1941, it was established that 25,000 Polish soldiers - airmen and specialists of various weapons, who were missing from the Polish Armed Forces in the West, would be evacuated from the Soviet Union. In addition to how to evacuate these people to Great Britain and the Middle East, General Anders faced another, even more serious problem. Namely, the Soviets began to pressure him to send the 5th Infantry Division to the front. It was the only reasonably armed division in Anders' Army. The rest of the units, neither the Russians nor the British, wanted to equip, and any talks on this subject had no effect. After unpleasant experiences in France in 1940, Poles defended themselves against the division of the Army and sending its individual divisions to the front. So General Anders, with the consent of General Sikorski, decisively refused.
      </p>

      <h4 class="fw-bold">Don't look back</h4>
      <p>
        
Several dozen minutes after the end of the conversation with Stalin, General Anders met with NKVD general Georgi Sergeyovich Zhukov, the representative of the Soviet government for the formation of Polish troops in the USSR. To the surprise of the Polish commander, Zhukov had with him a set of documents for the evacuation of about 30,000 soldiers and 10,000 members of their families, that is, the surplus of people for whom the Soviets did not provide food. As Professor
 Edward Kospath-Pawłowski emphasizes, "This supports the thesis that Stalin sought to evacuate Polish troops from the USSR, and the evacuation itself was to begin several dozen hours later [from the meeting with Anders - PK]." The first evacuation orders were given by General Anders in Moscow, on March 19. The evacuation was to pass through the base in Krasnowodsk, the commander of which the general appointed Lieutenant Colonel Zygmunt Berling. The first shipment left from here to Iran's Pehlewi on March 24. Let us give our voice to Artur Gruber, mentioned at the beginning: “On board the Soviet tanker we left the country of unfulfilled hopes and very sad, gray people without regrets. We left a cemetery behind. Epidemics of spotted and abdominal typhus and bloody dysentery took a heavy toll among exhausted Poles. Some say that there are more of them buried there than at Monte Cassino. On the second day in the evening we saw the shores of Iran. Pehlewi harbor was dimly lit by electric light. After disembarking, hearing the Polish speech of the bustling paramedics receiving the sick from our transport, I felt like I was in Poland, among my own, safe ”.

      </p>
      <p>
        This evacuation lasted until April 5. The soldiers of the 8th, 9th and 10th Infantry Divisions, an armored brigade, regiments of lancers and sappers, companies of railway engineers and most of the Army Organizational Center, surplus of the 6th and 7th Infantry Divisions reached Iran (then called Persia). In addition, all Polish airmen and sailors who were found in Russia among the living were embarked on Pehlewi.


      </p>

      <h4 class="fw-bold">We all go out</h4>
      <p>
        It quickly turned out that this was not the end of the evacuation of Polish troops from the Soviet Union. On May 14, Vyacheslav Molotov in a note to the Polish ambassador to Moscow, Stanisław Kot, stressed that he did not see the possibility of further enlisting in the Polish Army, since it was to be sent outside the USSR. This statement was the aftermath of Molotov's visit to London, during which - without the Poles knowing - the British proposed to move the Polish troops remaining in Russia to the Middle East. An advocate of such a solution was also General Anders, who sent reports to the staff of the Commander-in-Chief about the deteriorating supply situation of his troops. General Sikorski, on the other hand, still linked his hopes to the presence of Polish soldiers on the Eastern Front. As it turned out, the decision about the fate of the Polish Army in the USSR was made without the knowledge of both generals and regardless of their opinion on the subject. At the end of June 1942, Molotov sent a message to London that the Soviet government agreed to surrender the three Polish divisions remaining on Soviet territory. Churchill thanked Stalin for the Polish soldiers whom he "so kindly offered." The British prime minister had something to be happy about, as the Poles could provide him with cover for Iranian and Iraqi refineries and oil pipelines, which were strategic for the activities carried out on the African front. General Anders learned about these decisions with almost a month delay, and on July 26 he received an official letter in which it was emphasized that "The USSR Government agrees to meet the efforts of the Commander of the Polish Army in the USSR [...] to evacuate Polish troops from the USSR to the Middle East. and does not intend to put any obstacles in the immediate implementation of the evacuation. " On July 31, the Polish-Soviet military commission determined that about 70,000 military and civilians would leave Krasnowodzko to Pehlewi during August. The evacuation protocol signed by General Anders contained, inter alia, the sentence that "The Polish government does not consider it possible to use troops formed in the USSR on the Soviet-German front." This wording was used by the Soviets to actually suspend further recruitment to the Polish Army. In both evacuation operations, 116,543 people, including 78,631 military, were evacuated to the Middle East. For people who in this way could leave the "inhuman land", General Anders became a man of providence and, as Professor Czesław Brzoza accurately emphasized: "... the soldiers treated their commander as Moses, who led them out of the" house of slavery "."


      </p>

